$project: "Vrembem";
$description: "A BEM powered CSS component library.";
$version: "0.1.3";
$author: "Sebastian Nitu";
$repo: "https://github.com/sebnitu/vrembem.git";
$url: "http://sebnitu.com";
$year: 2017;
$license: "MIT";
@import 'vrem-core';
@include add-comment('container');

$container-max-width: 75rem !default;
$container-margin: 0 auto !default;
$container-padding: 1.5em !default;

.container {
  max-width: $container-max-width;
  margin: $container-margin;
  padding: 0 $container-padding;
}

.container_padding {
  padding: $container-padding;
}
